@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2"), url("../fonts/ptsans.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2"), url("../fonts/ptsans.woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  background-color: #ffffff;
  width: 100%;
  min-height: 56px;
  display: flex;
  justify-content: end;
  position: relative;
  padding: 0 18px 0 18px;
  box-sizing: border-box;
}
@media screen and (min-width: 768px) {
  .main-header {
    padding: 0;
  }
}

.main-header__logo {
  position: absolute;
  z-index: 1;
  cursor: pointer;
  left: 50%;
  margin-left: -92.5px;
}

.invitation {
  background-image: url("/img/images/back-mobile.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #97c5e4;
  box-sizing: border-box;
  padding: 90px 20px 85px 20px;
  text-align: center;
  position: relative;
  min-height: 290px;
}
@media (min-resolution: 2dppx) {
  .invitation {
    background-image: url("/img/images/images-@2x/back-mobile-2x@.jpg");
  }
}
.invitation::before {
  content: "";
  background-image: url("/img/images/decorative-treangles-mobile.svg");
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  bottom: -1px;
  width: 100%;
  height: 57px;
  right: 0;
}
@media screen and (min-width: 768px) {
  .invitation {
    background-image: url("/img/images/back-tablet.jpg");
    background-position: bottom;
  }
}
@media screen and (min-width: 768px) and (min-resolution: 2dppx) {
  .invitation {
    background-image: url("/img/images/images-@2x/back-tablet-@2x.jpg");
    background-size: cover;
  }
}
@media screen and (min-width: 768px) {
  .invitation::before {
    content: "";
    background-image: url("/img/images/decorative-treangles-tablet.svg");
  }
}
@media screen and (min-width: 1200px) {
  .invitation {
    background-image: url("/img/images/back-desktop.jpg");
    padding: 100px 20px 120px 20px;
  }
}
@media screen and (min-width: 1200px) and (min-resolution: 2dppx) {
  .invitation {
    background-image: url("/img/images/images-@2x/back-desktop-2x@.jpg");
    background-size: cover;
  }
}
@media screen and (min-width: 1200px) {
  .invitation::before {
    content: "";
    background-image: url("/img/images/decorative-treangles-desktop.svg");
  }
