.advantages {
  padding: 0;
  position: relative;
  padding-top: 20px;
  margin: 0;
  counter-reset: advantages;
}
.advantages::before {
  content: "";
  background-image: url("/img/images/decorative-figure-mobile.svg");
  position: absolute;
  top: 0;
  height: 37px;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .advantages::before {
    background-image: none;
  }
}
@media screen and (min-width: 768px) {
  .advantages {
    display: grid;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: 33.33% 33.33% 33.33%;
  }
}

.advantages__item {
  list-style: none;
}
.advantages__item .advantages__item--light-theme {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}
@media screen and (min-width: 768px) {
  .advantages__item {
    grid-column-start: 1;
    grid-column-end: -1;
  }
}

@media screen and (min-width: 768px) {
  .advantages__item--revert {
    grid-column-start: 1;
    grid-column-end: -1;
  }
}

@media screen and (min-width: 768px) {
  .advantages__item--light-theme {
    grid-column-start: 1;
    grid-column-end: -1;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: 1fr;
  }
}

